import React, { useEffect, useState } from 'react';
import { Dhamma } from './model'
import { ExpansionPanelSummary, ExpansionPanel, ExpansionPanelDetails, Typography, Theme, createStyles } from '@material-ui/core';
import { ExpandMore } from '@material-ui/icons';
import { Message } from '../message/message';
import { makeStyles } from '@material-ui/styles';
import { from } from 'rxjs';
import { map } from 'rxjs/operators'
import axios from 'axios';

export interface DhammaState {
  error: boolean,
  dhamma?: Dhamma
}

export interface DhammaProp {
  id: number,
  title: string
}

const useStyles = makeStyles((theme: Theme) => 
  createStyles({
    dhamma: {
      width: '100%',
      marginBottom: '1rem',
      [theme.breakpoints.up('sm')]: {
        marginBottom: '2rem'
      },
    },
    audios: {
      width: '100%',
      display: 'flex',
      flexWrap: 'wrap',
    },
    audio: {
      marginRight: '1rem'
    }
  })
);

const GET_ALL_DHAMMAS = "https://www.mraukoomeditationcenter.org/all-dhammas.json";
const S3_BASE_URL = 'https://mraukoomeditationcenter.s3-ap-southeast-1.amazonaws.com';

const DhammaItem: React.FC<DhammaProp> = ({ id, title }) => {

  const classes = useStyles();
  const [ state, setState ] = useState<DhammaState>();

  useEffect(() => {
    const subscription = from(axios.get(GET_ALL_DHAMMAS))
      .pipe(
        map(response => response),
        map(response => response.data),
      ).subscribe((dhammas: Dhamma[]) => {
        if(dhammas && id < dhammas.length) {
          setState({
            error: false,
            dhamma: dhammas[id]
          });
        }
      }, (error) => {
        setState({
            error: true
        });
      });
      return () => {
        subscription.unsubscribe();
      };
  }, [id]); 

  if(!state) {
    return (<Message type="Loading" />)
  }

  const { error, dhamma } = state;

  if(!dhamma) {
    return (<Message type="Loading" />)
  }

  if(error) {
    return (<Message type="Error" />)
  }

  return (
    <div className={classes.dhamma}>
      <ExpansionPanel>
        <ExpansionPanelSummary
          expandIcon={<ExpandMore />}
        >
          <Typography>{title}</Typography>
        </ExpansionPanelSummary>
        <ExpansionPanelDetails>
          <div className={classes.audios}>
            {
              dhamma.audios.map((audio, index) => (
                <div key={index} className={classes.audio}>
                  <p>{audio.title}</p>
                  <audio controls>
                    <source type="audio/mpeg" src={S3_BASE_URL+audio.location} />
                  </audio>
                </div>
              ))
            }
            </div>
        </ExpansionPanelDetails>
      </ExpansionPanel>
    </div>
  );
}

export default DhammaItem;