import React from 'react';
import { makeStyles, Theme, createStyles, createMuiTheme, ThemeProvider } from '@material-ui/core/styles';
import SwipeableDrawer from '@material-ui/core/SwipeableDrawer';
import Button from '@material-ui/core/Button';
import List from '@material-ui/core/List';
import Divider from '@material-ui/core/Divider';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import { Home as HomeIcon, Menu, Headset, LocationOn, FavoriteBorderOutlined, Facebook } from '@material-ui/icons';
import { Link as HyperLink, Avatar, Hidden } from '@material-ui/core';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import IconButton from '@material-ui/core/IconButton';
import { BrowserRouter, Route, Link } from 'react-router-dom';
import Home from './home/home';
import Contact from './contact/contact';
import DhammaList from './dhamma-list/dhamma-list';

const theme = createMuiTheme({
  palette: {
    primary: {
      main: "rgba(106, 0, 0, 0.9)"
    },
    text: {
      secondary: "#ffffff"
    }
  }
});

const useStyles = makeStyles((theme: Theme) => 
  createStyles({
    list: {
      width: 'auto',
    },
    fullList: {
      width: 'auto',
    },
    menuButton: {
      marginRight: theme.spacing(2),
    },
    menus: {
      color: theme.palette.text.secondary
    },
    menuLinks: {
      textDecoration: 'none',
    },
    title: {
      display: 'flex',
      alignItems: 'center',
      flexGrow: 1,
    },
    footerText: {
      display: 'flex',
      flexGrow: 1
    },
    footerTextHeart: {
      marginLeft: theme.spacing(0.5),
      marginRight: theme.spacing(0.5)
    },
    mainContent: {
      margin: '1rem',
      [theme.breakpoints.up('sm')]: {
          margin: '3rem',
      },
    },
    logo: {
      marginRight: '0.5rem'
    },
    drawerHeaderContainer: {
      backgroundColor: '#6a0000',
      minWidth: '12rem'
    },
    drawerHeaderLogo: {
      width: '24px',
      height: '24px'
    }
  })
);

const App: React.FC = () => {
  const classes = useStyles();
  const [state, setState] = React.useState({
    open: false,
  });

  const openDrawer = () => (event: React.KeyboardEvent | React.MouseEvent) => {
    setState({ ...state, open: true });
  };

  const closeDrawer = () => (event: React.KeyboardEvent | React.MouseEvent) => {
    setState({ ...state, open: false });
  };

  return (
    <BrowserRouter>
      <ThemeProvider theme={theme}>
        <AppBar position="static">
          <Toolbar>
            <IconButton edge="start" className={classes.menuButton} color="inherit" aria-label="menu" onClick={ openDrawer() }>
              <Menu />
            </IconButton>
            <Typography variant="h6" className={classes.title}>
              <Avatar alt="Mrauk Oo Meditation Center" src="/logo192-white.png" className={classes.logo} />
              <Hidden smDown><HyperLink underline="none" component={Link} to={'/'} color="inherit">Mrauk Oo Meditation Center</HyperLink></Hidden>
            </Typography>
            <Typography variant="body2">
              {/* <Button component={Link} to={'/'} color="inherit" startIcon={<HomeIcon />}>Home</Button> */}
              <Button component={Link} to={'/dhamma'} color="inherit" startIcon={<Headset />}>Dhamma</Button>
              {/* <Button component={Link} to={'/contact'} color="inherit" startIcon={<LocationOn />}>Contact</Button> */}
            </Typography>
          </Toolbar>
        </AppBar>
        
        <section className={classes.mainContent}>
          <Route path="/" component={Contact} exact />
          <Route path="/dhamma" component={DhammaList} />
          <Route path="/contact" component={Contact} />
        </section>

        <SwipeableDrawer
          anchor="left"
          open={state.open}
          onClose={closeDrawer()}
          onOpen={openDrawer()}
        >
          <div
            className={classes.list}
            onClick={closeDrawer()}
            onKeyDown={closeDrawer()}
          >
            <List className={classes.drawerHeaderContainer}>
                <ListItem button key="home" component={Link} to={'/'}>
                  <ListItemIcon><Avatar alt="Mrauk Oo Meditation Center" src="/logo192-white.png" className={classes.drawerHeaderLogo} /></ListItemIcon>
                  <Typography variant="h6"><HyperLink underline="none" component={Link} to={'/'} color="textSecondary">Mrauk Oo Meditation Center</HyperLink></Typography>
                </ListItem>
            </List>
            <Divider />
            <List>
                <ListItem button key="dhamma" component={Link} to={'/dhamma'}>
                  <ListItemIcon><Headset /></ListItemIcon>
                  <ListItemText primary="Dhamma" />
                </ListItem>
            </List>
            {/* <Divider />
            <List>
              <ListItem button key="contact" component={Link} to={'/contact'}>
                  <ListItemIcon><LocationOn /></ListItemIcon>
                  <ListItemText primary="Contact" />
                </ListItem>
            </List> */}
            <Divider />
          </div>
        </SwipeableDrawer>

        <AppBar position="static">
          <Toolbar >
            <div className={classes.footerText}>
              <div>Made with</div><FavoriteBorderOutlined className={classes.footerTextHeart} /><div>in NYC</div> 
            </div>
            <div>
              <HyperLink target="_blank" rel="noopener" href="https://www.facebook.com/mraukoosayataw" color="inherit"><Facebook fontSize="large" /></HyperLink>
            </div>
          </Toolbar>
        </AppBar>
      </ThemeProvider>
    </BrowserRouter>
  );
}

export default App;
