import React, { useEffect, useState } from 'react';
import DhammaItem from '../dhamma/dhamma-item';
import { Dhamma } from '../dhamma/model';
import { Message } from '../message/message';
import { createStyles, Theme, Typography, IconButton } from '@material-ui/core';
import { makeStyles } from '@material-ui/styles';
import { from } from 'rxjs';
import { map, last } from 'rxjs/operators'
import axios from 'axios';
import { More, MoreVert } from '@material-ui/icons';

// export interface DhammaListState {
//   error: boolean,
//   dhammas: Dhamma[],
//   lastIndex: number
// }

const useStyles = makeStyles((theme: Theme) => 
  createStyles({
    mainContent: {
      minHeight: 'calc(100vh - 14rem)',
      display: 'flex',
      justifyContent: 'space-around',
      flexWrap: 'wrap'
    },
    dhammaList: {
      width: '100%',
      display: 'flex',
      flexWrap: 'wrap'
    },
    header: {
      marginBottom: '2rem'
    },
    moreContainer: {
      display: 'flex',
      width: '100%',
      justifyContent: 'center'
    }
  })
);

const GET_ALL_DHAMMAS = "https://www.mraukoomeditationcenter.org/all-dhammas.json";

const DhammaList: React.FC = () => {

  const classes = useStyles();
  let [ loading, setLoading ] = useState<boolean>();
  let [ lastIndex, setLastIndex ] = useState<number>();
  let [ end, setEnd ] = useState<boolean>();
  let [ dhammas, setDhammas ] = useState<Dhamma[]>();

  const observable = from(
    axios.get(GET_ALL_DHAMMAS))
      .pipe(
        map(response => response),
        map(response => response.data),
      );

  const loadMore = () => {
    setLoading(true);
    setTimeout(() => {
      setLastIndex(lastIndex ? lastIndex+5 : 10);   
      setLoading(false);   
    }, 3000);
  };

  useEffect(() => {
    const subscription = observable.subscribe((dhammas: Dhamma[]) => {
      if(lastIndex) {
        setDhammas(dhammas.slice(0, lastIndex));
        setEnd(dhammas.length <= lastIndex);
      } else {
        setDhammas(dhammas.slice(0, 5));
      }
    }, (error) => {
      setDhammas([]); 
    });
    return () => {
      subscription.unsubscribe();
    };
  }, [lastIndex]);

  if(!dhammas) {
    return (<div className={classes.mainContent}><Message type="Loading" /></div>)
  }

  if(dhammas.length === 0) {
    return (<Message type="Error" />)
  }

  return (
    <div>
      {/* <Typography variant="h6" className={classes.header}>Dhamma</Typography> */}
      <div className={classes.dhammaList}>
        {
          dhammas.map((dhamma, index) => (
            <DhammaItem key={index} id={index} title={dhamma.title} />
          ))
        }
      </div>
      { !end && loading  && <Message type="Loading" /> }
      { !end && !loading && <div className={ classes.moreContainer }><IconButton edge="start" color="inherit" aria-label="menu" onClick={ loadMore }><MoreVert></MoreVert></IconButton></div> }
    </div>
  );
}

export default DhammaList;